nav {
  // background-color: rgb(48, 48, 48);

  display: flex;

  justify-content: space-between;
  align-items: center;

  margin-bottom: 10px;
  position: sticky;
  top: 0;
  max-width: 1400;
}

.nav-link {
  color: white;
  text-decoration: none;
  margin-right: 40px;
  font-size: 20px;
  font-weight: bold;

  &:visited {
    color: white;
  }

  &:last-child {
    margin-right: 0;
  }
}

.logo-image {
  width: 100px;
  height: 40px;
}

.fa-mag {
  color: white;
  padding: 0 10px;
  align-self: center;
}

.search-container {
  display: flex;

  margin-right: 20px;
  border: solid white 2px;
}

.logo-container {
  display: flex;
  justify-self: center;
}

.music {
  padding-bottom: 10px;
  font-size: 30px;
  text-align: center;
  margin-left: 5px;
  color: purple;
}

.image {
  margin-top: 5px;
  margin-left: 20px;
}

.menu-icon {
  display: none;
}

.main-div-flex-center {
  display: flex;
  flex-direction: column;
}
.outlet-container {
  align-self: center;
}

@media screen and (max-width: 866px) {
  .NavbarItems {
    position: relative;
    margin-bottom: 120px;
  }

  // .logo-menu_items-container {
  //   display: flex;
  //   justify-content: space-between;
  // }

  .nav-menu {
    display: flex;
    flex-direction: column;
    // align-items: center;
    // justify-content: center;
    width: 100%;
    height: 1000px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #000000;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    overflow: hidden;
  }

  .nav-link {
    text-align: center;
    padding: 2rem;
    width: 80%;
    display: table;
  }

  .logo-container {
    position: absolute;
    top: -10px;
    left: -40px;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    font-size: 40px;
    color: white;
    position: absolute;
    top: -9px;
    right: 0;
    transform: translate(-100%, 60%);
    cursor: pointer;
    font-size: 1.8rem;

    .fa-bars {
      font-size: 25px;
    }
  }

  .search-container {
    position: absolute;
    top: 70px;
    left: 14px;
    transform: translate(25%, 50%);
    width: 60%;

    .search-box {
      width: 100%;
    }
  }
  .album-image-page {
    height: 400px;
    width: 20px;
    margin-bottom: 80px;
  }
}
