.album-container-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center;
}

.albumName {
  color: white;
}

.album-h1-title {
  color: white;
  text-decoration: none;
}

.album-image-page {
  height: 400px;
  width: 400px;
  margin-bottom: 20px;
}

.album-artist_link {
  margin-bottom: 80px;
  color: white;
  font-size: 2rem;
  text-decoration: none;
  text-align: center;
  :visited {
    text-decoration: none;
  }
}
.zip-container {
  display: flex;
  flex-direction: column;
  height: fit-content;
  width: 400px;
  border-top: 5px white solid;
  border-bottom: 5px white solid;
  row-gap: 5px;

  background-color: rgba(45, 45, 45, 0.71);
  .zip-container-link {
    text-align: center;
    color: white;
    font-size: 1.4rem;

    margin: 3px 10px;
  }

  .zip-container-title {
    color: white;
    font-size: 20px;
    align-self: center;
  }
}

.album-h1-title {
  font-size: 2rem;
  margin-bottom: 25px;
}

@media screen and (max-width: 866px) {
  .zip-container {
    display: flex;
    flex-direction: column;
    height: fit-content;
    width: 50%;
    border-top: 5px white solid;
    border-bottom: 5px white solid;
    row-gap: 5px;
  }

  .album-image-page {
    height: 80%;
    width: 80%;
  }
}
