.card-contianer {
  background-color: transparent;
  display: flex;
  flex-wrap: wrap;

  // overflow-y: hidden;
  // overflow-x: scroll;

  // max-width: 1400px;
  width: fit-content;

  justify-content: center;
  align-items: center;

  //   grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 20px;
}
.card-box {
  margin: 0;
  height: 250px;
  width: 150px;
  margin-left: 30px;

  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  border-radius: 15px;
  background-color: rgb(12, 12, 12);
  margin-top: 10px;
  margin-bottom: 10px;
}

.album-image {
  width: 150px;

  background-size: cover;
  background-position: center;
  height: 150px;
}
p {
  margin: 0;
  padding: 0;
}

.album-title {
  font-weight: bold;
  font-size: 15px;
  margin: 10px 0;
  text-align: center;
}

.artist-name {
  text-align: center;
}

.card-list-link {
  color: white;
  text-decoration: none;

  &:visited {
    color: white;
  }

  &:hover {
    cursor: pointer;
  }
}

h2 {
  color: white;
  margin-bottom: 40px;
}

@media screen and (max-width: 866px) {
  .card-box {
    margin: 0;
    height: 250px;
    width: 130px;
    margin-left: 30px;

    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    border-radius: 15px;
    background-color: rgb(12, 12, 12);
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .album-image {
    width: 130px;
    background-size: cover;
    background-position: center;
    height: 130px;
  }

  .album-title {
    font-weight: bold;
    font-size: 15px;
    margin: 10px 0;
    text-align: center;
  }

  .card-contianer {
    gap: 0;
  }
}

